.img-slide-responsive {
  align-self: center;
  vertical-align: middle;
  align-items: center;
  align-content: center;
  margin-left: 4%;
  margin-right: 4%;
}

.img-slide-responsive img{
  width:  210px;
  height: 90px;
  margin: 0px;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-right: 50px;
  object-fit: cover;
  margin-left: 30%;
}
.box{
  position: relative;
  display: inline-block; /* Make the width of box same as image */
}
.box .centered {
  position: absolute;
  top: 40%;
  left: 67%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.centered img {
  width: 70%;
  height: 70%;
  display: none;
}
.box:hover .centered img {
  display: inline-block;
}

.flex-container-settings {
  padding: 0;
  margin: 0;
  list-style: none;
  border: none;
  -ms-box-orient: horizontal;
