  overflow: auto;
}
.wrap-settings {
  flex-wrap: wrap;
  height: 100%;
}
.flex-item-settings-bar {
  background-color: #7a4cd1;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: auto;
  width: 20%;
  height: 100%;
  margin-right: 5%;
  
  color: white;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  border: 1px ridge #bd97fa;
  border-radius: 2px;
  vertical-align: middle;
}
.flex-item-settings-bar h1 {
  font-size: 25px;
  color: #ffffffd2;
  padding-top: 12%;
  padding-bottom: 5%;
  border-bottom: 1px solid #ffffff98;
  overflow: auto;
}
.flex-item-settings-bar h2 {
  font-size: 20px;
  font-weight: 500;
  color: #ffffffd2;
  padding-top: 0%;
  padding-bottom: 5%;
  border-bottom: 1px groove #ffffffd2;
  overflow: auto;
}
.flex-item-settings-account {
  background-color: #5A26BA;
  width: 70%;
