}
.flex-item-settings-account h1 {
  font-size: 30px;
  color: #fffffff4;
  padding-top: 4%;
  padding-left: 1%;
  text-align: left;
}

.site-collapse-custom-collapse, .site-collapse-custom-panel {
  background: #ffffff98;
  font-size: 20px;
  text-align: left;
  border-radius: 2px;
  border: none;
  overflow: hidden;
}
.react-player__preview, .react-player__shadow, .react-player__play-icon {
  outline: none;
}
.videoStyle {
  height: 1000 !important;
}





